import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import HomeView from "../views/HomeView.vue";
import model from "model";
import WelcomeView from "../views/home/WelcomeView.vue";

Vue.use(VueRouter);

export const menus: Array<RouteConfig> = [
  {
    path: "bar_chart",
    name: "BarChart",
    component: () => import("mfe_chart/BarChart.vue"),
  },
];

export const details: Array<RouteConfig> = [
  {
    path: "bar_chart_detail/:id",
    name: "BarChartDetail",
    component: () => import("mfe_chart/BarChartDetail.vue"),
    meta: {
      isMultipleTab: true,
    },
  },
];

const routes: Array<RouteConfig> = [
  {
    path: "/",
    redirect: "welcome",
    name: "Home",
    component: HomeView,
    children: [
      {
        path: "welcome",
        name: "Welcome",
        component: WelcomeView,
      },
      ...menus,
      ...details,
    ],
  },
];

const router = new VueRouter({
  routes,
});

router.beforeEach((to, from, next) => {
  // 
  model.dashboard.openTab(to);
  next();
});

router.onError((error) => {
  console.error(error);
  const pattern = /Loading chunk ([\S])+ failed/g;
  const isChunkLoadFailed = error.message.match(pattern);
  if (isChunkLoadFailed) {
    // replaceF5jsreplace location.reload F5
    location.reload();
  }
});

export default router;
